.profile {
    margin: 42px 19px 0;
    padding: 0;
    min-width: 282px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

@media screen and (min-width: 880px) {
    .profile {
        flex-direction: row;
        width: 880px;
        margin: 40px auto 0;
    }
}